@import '../../styles/_variables.scss';
@import '../../styles/_mixins.scss';
.login-container {
  margin-top: 35vh;
    .title-small {
  font-weight: 700;
  font-size: 24px;
  line-height: 35.27px;
  text-align: center;
    }
  .title-big {
    font-weight: 1200;
    font-size: 52px;
    margin-bottom: 4rem;
    text-align: center;
  
      }
    .login-form {
      display: flex;
      flex-direction: column;
      margin-left: 2rem;
      margin-right: 2rem;
      border-radius: 1rem;
      background: rgba(255, 255, 255, 0.1);
      padding: 1.5rem;
      backdrop-filter: blur(24px);
  
      label {
        margin-bottom: 0.25rem;
        font-weight: 400;
        font-size: 16px;
        line-height: 23.52px;
        letter-spacing: 0%;
        text-align: right;
        margin-left: 2rem;
        margin-right: 2rem;
      }
  
      input {
        padding: 0.6rem;
        margin-bottom: 1rem;
        border: 1px solid #ccc;
        border-radius: 8px;
        background: rgba(255, 255, 255, 0.3);
        font-size: 1rem;
        margin-left: 2rem;
        margin-right: 2rem;
      }
  
      .error-msg {
        color: $error-color;
        margin-bottom: 1rem;
        text-align: center;
        font-size: 0.9rem;
      }
  
      button {
        border: 1px solid rgba(255, 255, 255, 1);
        border-radius: 4px;
    padding: 9px 0px;
        color:  rgba(255, 255, 255, 1);
font-weight: 600;
font-size: 16px;
line-height: 23.52px;

text-align: center;
margin-right: 2rem;
    margin-left: 2rem;
    background: rgba(255, 255, 255, 0.1);

        &:hover {
          background-color: darken($primary-color, 10%);
        }
      }
    }
  
    .register-container2 {

      display: flex;  // Ensures elements stay on the same line
      align-items: center;  // Aligns text and link vertically
      gap: 8px;  // Adds some spacing between text and link
    }
    
    .register-text {
      color: #ccc;
      margin-right: 3rem;
      font-weight: 400;
      font-size: 14px;
      line-height: 20.58px;
      text-align: right;
      
    }
    
    .register-link {
      color: white;
      font-weight: 400;
      font-size: 14px;
      line-height: 20.58px;
      letter-spacing: 0%;
      text-align: right;
      text-decoration: underline;
      text-decoration-style: solid;
      text-decoration-thickness: Auto;
      text-decoration-skip-ink: auto;
      
    
      &:hover {
        text-decoration: underline;
      }
    }
    
  }
  